import React from 'react';
import LoadingButton from '@mui/lab/LoadingButton';

export default function Main(props) {
    return (
        <div>
            <div className="about-us-area pd-100" style={{ paddingTop: 50 }}>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-5 col-md-8 align-self-center">
                            <div className="about-us-video">
                                <img className="thumb" src="assets/img/video/1.png" alt="img" />
                                <a rel="noopener" className="play-btn" href="https://www.youtube.com/watch?v=Iw2ZYBS0lTo" data-effect="mfp-zoom-in"><img src="assets/img/video/play-btn.png" alt="img" /></a>
                            </div>
                        </div>
                        <div className="col-lg-6 offset-lg-1">
                            <div className="about-us-details">
                                <div className="section-title">
                                    <h2 className="title">Nothing is impossible. We can help you achieve your goals!</h2>
                                    <p>
                                        We at Bangko sa Balay Foundation Inc. continues to provide quality services and meaningful transactions that will surely help our clients.
                                    </p>
                                </div>
                                <a rel="noopener" className="btn btn-blue" href="/">Learn More</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="fun-fact-area">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-lg-8 text-center">
                            <div className="section-title section-title-2">
                                <h6 className="subtitle subtitle-thumb">Why Choose Us</h6>
                                <h2 className="title">Come and be part of our family</h2>
                                <p>Since we started in 2007, we have grown into a much bigger and better organization.</p>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-center">
                        <div className="col-lg-4 col-sm-6 text-center">
                            <div className="single-fact">
                                <h1 className="counter">5000+</h1>
                                <h5>Clients</h5>
                                <p>achieve their goals</p>
                            </div>
                        </div>
                        <div className="col-lg-4 col-sm-6 text-center">
                            <div className="single-fact">
                                <h1 className="counter">10</h1>
                                <h5>Our Branches</h5>
                                <p>all over Mindanao</p>
                            </div>
                        </div>
                        <div className="col-lg-4 col-sm-6 text-center">
                            <div className="single-fact">
                                <h1 className="counter">5243+</h1>
                                <h5>Transactions</h5>
                                <p>and more...</p>
                            </div>
                        </div>
                        <div className="col-lg-12 text-center">
                            <a rel="noopener" className="btn" href="/" onClick={(e) => props.handleNavigation(e, "/new-account")}>Open An Account</a>
                        </div>
                    </div>
                </div>
            </div>

            <div className="service-area default-pd">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-lg-8 text-center">
                            <div className="section-title">
                                <h6 className="subtitle subtitle-thumb">Services</h6>
                                <h2 className="title">Here are our list of Services</h2>
                                <h2 className="title">That are Right For You</h2>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-center">
                        <div className="col-lg-4 col-md-6">
                            <div className="single-service">
                                <div className="thumb">
                                    <img src="assets/img/service/06.png" alt="img" />
                                </div>
                                <div className="service-details">
                                    <h5><a rel="noopener" href="/">Loans</a></h5>
                                    <ul>
                                        <li>Backyard broiler production project</li>
                                        <li>Small business loan</li>
                                        <li>Regular loan</li>
                                    </ul>
                                    <a rel="noopener" className="angle-btn" href="/"><img src="assets/img/icon/angle-left-round.png" alt="icon" /></a>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-6">
                            <div className="single-service">
                                <div className="thumb">
                                    <img src="assets/img/service/04.png" alt="img" />
                                </div>
                                <div className="service-details">
                                    <h5><a rel="noopener" href="/">Savings</a></h5>
                                    <ul>
                                        <li>Volountary savings</li>
                                        <li>Compulsory savings</li>
                                        <li>Progressive savings</li>
                                        <li>Kiddie savings</li>
                                        <li>Kalambuan savings</li>
                                    </ul>
                                    <a rel="noopener" className="angle-btn" href="/"><img src="assets/img/icon/angle-left-round.png" alt="icon" /></a>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-6">
                            <div className="single-service">
                                <div className="thumb">
                                    <img src="assets/img/service/02.png" alt="img" />
                                </div>
                                <div className="service-details">
                                    <h5><a rel="noopener" href="/">Microinsurance</a></h5>
                                    <ul>
                                        <li>Mutual benefit association</li>
                                        <li>Retirement savings fund</li>
                                    </ul>
                                    <a rel="noopener" className="angle-btn" href="/"><img src="assets/img/icon/angle-left-round.png" alt="icon" /></a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="container">
                <div className="sign-up-area">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="media align-items-center">
                                <div className="media-left">
                                    <i className="fa fa-envelope-o"></i>
                                </div>
                                <div className="media-body">
                                    <h5>Sign Up For Newsletter</h5>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <form className="d-md-inline-flex">
                                <div className="form-group">
                                    <input type="email" className="form-control" id="exampleInputEmail1" placeholder="Your mail here" />
                                </div>
                                <LoadingButton type="submit">Submit</LoadingButton>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
